@use "./scss/header";
:root {
	--mainBg: salmon;
}

$light-text: moccasin;

body {
	background-color: salmon;
	background-color: var(--mainBg);
	color: $light-text;
}
.container {
	padding: 1rem;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: -webkit-gradient(linear,  left bottom, left top,  from(black), to(white));
	background: linear-gradient(to top,  black, white);
}
.container h1 {
		font-size: 3em;
		text-align: center;
	}
